/* How It Works */

.works {
    background-color: #181f2a;
    padding: 20px 0 var(--section-padding);
}

.works .container {
    display: flex;
    justify-content: space-between;
    gap: 50px;
    align-items: center;
}

@media (max-width: 1189px) {
    .works .container {
        flex-direction: column;
        text-align: center;
        justify-content: center;
        align-items: center;
    }

    .works .link {
        text-align: center;
        justify-content: center;
    }
}

.works img {
    max-width: 100%;
    height: auto;
    flex-basis: 45%;
}

.works .text {
    flex-basis: 55%;
}

.works .text h2 {
    font-size: 35px;
    line-height: var(--line-height);
}

.works .text p {
    font-size: 25px;
    line-height: var(--line-height);
    color: #d6d6d6;
}

.works .link {
    display: flex;
    align-items: center;
    gap: 10px;
}

.works .link span {
    transform: scale(1.8);
}

.works .link span svg {
    display: block;
    max-width: 100%;
}

.works .link a {
    color: var(--cyan);
    font-size: 18px;
    transition: var(--main-transition);
    position: relative;
}

.works .link a:hover {
    text-decoration: underline;
}