/* Global Rules */

*, *::before, *::after {
    box-sizing: border-box;
}

body {
    font-size: var(--font-size);
    font-family: 'Raleway';
    background-color: #1C2431;
    color: #fff;
    font-weight: 400;
}

html {
    scroll-behavior: smooth;
}

html::-webkit-scrollbar-track
{
	background-color: #1C2431;
}

html::-webkit-scrollbar
{
	width: 16px;
	background-color: #1C2431;
}

html::-webkit-scrollbar-thumb
{
	background-color: #37A5CE;
    border-radius: 6px;
}

ul, ol, dl, li {
    list-style: none;
}

a, a:visited {
    text-decoration: none;
}

h2,
h3,
nav,
.cta {
    font-weight: 700;
}

.container {
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
    min-height: 97px;
}

button {
    cursor: pointer;
}

/* Small */

@media (min-width: 768px) {
    .container {
        width: 750px;
    }
}

/* Medium */

@media (min-width: 992px) {
    .container {
        width: 970px;
    }
}

/* Large */

@media (min-width: 1200px) {
    .container {
        width: 1190px;
    }
}