/* Features */

.features {
    background-color: #181f2a;
    padding: 20px 0 var(--section-padding);
}

.features .container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 5.6rem;
}

@media (max-width: 670px) {
    .features .container {
        grid-template-columns: 1fr;
        margin: auto;
        max-width: 400px;
        display: grid;
    }
}

.features .box {
    max-width: 470px;
    text-align: center;
}

.features .box .text p {
    line-height: var(--line-height);
    font-size: 18px;
}

.features .box .text h2 {
    margin: 0.5em 0;
}