/* Navbar */

.navbar {
    padding: 20px 20px;
    background-color: var(--intro-email-bg);
}

.navbar .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar .logo img {
    max-width: 130px;
    height: auto;
}

.navbar .links ul {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar .links ul a {
    padding-right: 50px;
    color: #b5b5b5;
    transition: var(--main-transition);
    font-size: 18px;
}

.navbar .links ul a:hover {
    color: #fff;
}

@media (max-width: 578px) {
    .navbar .container {
        flex-direction: column;
        justify-content: center;
        gap: 20px;
    }

    .navbar .logo img {
        max-width: 110px;
    }

    .navbar .links ul a {
        font-size: 16px;
        padding-right: 20px;
    }
}