/* Sign In */

.signIn {
    background-color: #181f2a;
    padding: 20px 0 var(--section-padding);
    position: relative;
    z-index: 999;
}

.signIn .container {
    background-color: #1C2431;
    padding: 50px 70px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 30px;
    border-radius: 20px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25), 0 2px 2px rgba(0, 0, 0, 0.20), 0 4px 4px rgba(0, 0, 0, 0.15), 0 8px 8px rgba(0, 0, 0, 0.10), 0 16px 16px rgba(0, 0, 0, 0.05);
}

.signIn .text {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.signIn .text h2 {
    font-size: 30px;
    font-weight: bold;
}

.signIn .text p {
    line-height: var(--line-height);
    font-weight: 300;
    font-size: 18px;
    letter-spacing: 1.2px;
    color: #d6d6d6;
}

.signIn .form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
}

@media (max-width: 570px) {
    .signIn .form {
        flex-direction: column;
        justify-content: center;
    }

    .signIn button {
        width: 100%;
    }
}

.signIn .form input {
    color: #858383;
    font-size: 17px;
    padding: 12px 32px;
    border: none;
    outline: none;
    border-radius: 20px;
}

.signIn .form button {
    font-size: 18px;
    border: none;
    border-radius: 20px;
    color: #fff;
    background: linear-gradient(to right, var(--cyan), var(--blue));
    padding: 12px 20px;
}

.signIn .form button:hover {
    color: #fff;
    background: var(--blue);
}