/* Variables */

:root {
    /* Colors */
    --cyan: hsl(176, 68%, 64%);
    --blue: hsl(198, 60%, 50%);
    --light-red: hsl(0, 100%, 63%);
    --main-bg: hsl(218, 18%, 13%);

    /* Typography */
    --font-size: 14px;
    --line-height: 1.9;

    /* Spacing */
    --section-padding: 12em;

    /* Animating */
    --main-transition: 0.5s
}