footer {
    padding: 40px;
    background-color: #0B1523;
    position: relative;
}

footer .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@media (max-width: 430px) {
    footer .container {
        flex-direction: column;
    }

    footer .social {
        padding-top: 24px;
    }
}

footer .container .logo {
    max-width: 130px;
}

footer .social {
    display: flex;
    align-items: center;
    gap: 20px;
}

footer .social div img {
    width: 30px;
}


footer .social div {
    cursor: pointer;
}

footer .copyright-text {
    text-align: center;
    letter-spacing: 1.1px;
    font-size: 15px;
    padding-bottom: 2px;
}

footer hr {
    height: 0px;
    border: 1px solid #fff;
}