.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    gap: 35px;
    padding: var(--section-padding) 0;
    background-image: url("../../images/bg-curvy-desktop.svg");
    background-position: center bottom;
    background-repeat: no-repeat;
    padding-top: 2em;
    background-size: contain;
}

.intro img {
    width: 100%;
}

.intro .text {
    max-width: 800px;
}

.intro .text h2 {
    font-size: 35px;
    line-height: var(--line-height);
}

.intro .text p {
    font-size: 25px;
    line-height: var(--line-height);
    color: #d6d6d6;
}

@media (max-width: 425px) {
    .intro .text h2 {
        font-size: 30px;
    }
    
    .intro .text p {
        font-size: 19px;
    }    
}

.intro button {
    border-radius: 60px;
    background-color: var(--blue);
    color: #fff;
    padding: 20px 45px;
    font-weight: bold;
    font-size: 18px;
    transition: var(--main-transition);
    border: none;
}

.intro button:hover {
    background-color: #fff;
    color: var(--main-bg);
}
