/* Reviews */

.reviews {
    background-color: #181f2a;
    padding: 20px 0 var(--section-padding);
}

.reviews .container {
    display: flex;
}

.reviews .img {
    position: relative;
}

.reviews .img img {
    position: absolute;
    top: -12px;
    right: -25px;
}

@media (max-width: 820px) {
    .reviews .img img {
        display: none;
    }
}

.reviews .boxes {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    z-index: 999;
}

.reviews .boxes .box {
    background-color: #1C2431;
    padding: 24px 32px;
    border-radius: 10px;
    margin: 80px 0;
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin: 10px;
}

.reviews .box .text {
    line-height: var(--line-height);
    font-weight: 300;
    font-size: 18px;
    letter-spacing: 1.2px;
}

.reviews .box .user {
    display: flex;
    align-items: center;
    justify-content: start;
    gap: 20px;
}

.reviews .box .user img {
    border-radius: 50%;
    width: 60px;
}

.reviews .box .user .info {
    margin-top: 8px;
}